<template>
  <div>
    <div>
      <nav class="panel">
        <div class="panel-block">
          <p class="control has-icons-left">
            <label>
              <input
                class="input"
                type="search"
                placeholder="Start type the address"
                v-model="query"
                @keyup="searchAddress"
              />
            </label>
            <span class="icon is-left">
              <i class="fas fa-search" aria-hidden="true"></i>
            </span>
          </p>
        </div>
        <a
          class="panel-block"
          v-for="address in addresses"
          :key="address.place_id"
          @click="selectAddress(address)"
        >
          {{ address.display_name }}
        </a>
        <div class="panel-block" v-if="addresses.length > 0">
          <button
            class="button is-link is-outlined is-fullwidth is-small"
            @click="clearSearch"
          >
            clear search
          </button>
        </div>
      </nav>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      query: "",
      addresses: [],
      timer: null
    };
  },
  computed: {
    zoom() {
      return this.$store.getters.zoom;
    }
  },
  methods: {
    searchAddress() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }

      if (!this.query) {
        return;
      }

      const params = {
        q: this.query,
        format: "json"
      };

      this.timer = setTimeout(() => {
        const url = `https://nominatim.openstreetmap.org/search`;
        axios
          .get(url, { params })
          .then(result => result.data)
          .then(result => {
            this.addresses = result;
          });
      }, 500);
    },
    selectAddress(address) {
      const latLng = {
        lat: address.lat,
        lng: address.lon
      };
      this.$store.dispatch("setZoom", 15);
      this.$store.dispatch("setLatLng", latLng);
      this.$store.dispatch("addToHistory", address);
    },
    clearSearch() {
      this.query = "";
      this.addresses = [];
    }
  }
};
</script>
